<script setup lang="ts">
import { useAsyncState, useVModel } from "@vueuse/core";
import type { PxlIcon } from "~/components/U/Icon";

const props = withDefaults(
  defineProps<{
    modelValue?: boolean;
    icon?: PxlIcon;
    heading?: string;
    description?: string;
    cancelLabel?: string;
    confirmLabel?: string;
    confirmFn?: (() => void) | (() => Promise<void>);
    cancelFn?: (() => void) | (() => Promise<void>);
    variant?: "confirm" | "delete";
  }>(),
  {
    icon: "check",
    heading: "Are you sure you want to confirm this?",
    cancelLabel: "Cancel",
    confirmLabel: "Confirm",
    variant: "confirm",
  },
);

const emit = defineEmits(["update:modelValue"]);
const isOpen = useVModel(props, "modelValue", emit);
const closeModal = () => (isOpen.value = false);
const confirmVariant = computed(() => props.variant === "delete" ? "danger" : "primary");
const { isLoading, execute: onAsyncClick } = useAsyncState(
  (fn: (() => void) | (() => Promise<void>)) => Promise.resolve().then(fn),
  undefined,
  { immediate: false, onSuccess: closeModal, onError: closeModal },
);

function onActionClick(action: "confirm" | "cancel"): Promise<void> {
  if (action === "confirm" && props.confirmFn)
    return onAsyncClick(0, props.confirmFn);
  else if (action === "cancel" && props.cancelFn)
    return onAsyncClick(0, props.cancelFn);
  return onAsyncClick(0, Promise.resolve);
}
</script>

<template>
  <UModal v-model="isOpen">
    <div class="p-4">
      <div class="mb-1 text-end">
        <BaseButton title="close" name="close" type="icon" @click="closeModal()">
          <IconSearchClose class="relative z-10" />
        </BaseButton>

        <!-- <UButton title="Close" variant="link-secondary" icon="close" class="h-5 w-5" @click="closeModal()" /> -->
      </div>

      <div class="flex flex-col gap-6 md:px-6 md:pb-6">
        <div class="text-center">
          <div class="rounded-full bg-[var(--base-icon-logo)] h-16 w-16 inline-grid place-items-center relative">
            <IconTag v-if="props.icon === 'tag'" :width="24" :height="24" />
            <IconClip v-else-if="props.icon === 'clip'" :width="24" :height="24" />
            <IconHighlight v-else-if="props.icon === 'highlight'" :width="24" :height="24" />

            <div v-if="variant === 'delete'" class="h-6 w-6 bg-red-500 rounded-full grid place-items-center absolute bottom-0 -right-2">
              <IconClose :width="12" :height="12" fill="#ffff" />
            </div>
          </div>
        </div>
        <div class="text-center text-black dark:text-white space-y-4">
          <h1 class="text-2xl font-bold" v-text="heading" />
          <p class="text-sm font-medium" v-text="description" />
        </div>
        <div class="flex gap-4">
          <BaseButton
            :title="cancelLabel"
            :name="cancelLabel"
            type="secondary"
            class="flex-1 font-bold"
            @click="onActionClick('cancel')"
          >
            {{ cancelLabel }}
          </BaseButton>

          <BaseButton
            :title="confirmLabel"
            :name="confirmLabel"
            :type="confirmVariant"
            class="flex-1 font-bold"
            :disabled="isLoading"
            @click="onActionClick('confirm')"
          >
            {{ confirmLabel }}
          </BaseButton>
        </div>
      </div>
    </div>
  </UModal>
</template>
